html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
}

ol, ul {
  list-style: none;
}

.App {
  display: flex;
  flex-direction: column;
  justify-items: center;
  color: #fff;
  min-width: 40vw;
  min-height: 80vh;
  border: 0.3vmin solid #fff;
  border-radius: 3vmin;
  padding: 2vmin;
  background-color: hsla(0, 0%, 0%, 0.2);
}
h1 {
  font-size: 6vmin;
}
button {
  min-width: 20vmin;
  height: 5vmin;
  border-radius: 1vmin;
  border: 0.3vmin solid #000;
  color: #000;
  background: linear-gradient(180deg, hsl(0, 0%, 90%), hsl(0, 0%, 25%));
  font-size: 2.5vmin;
  transition: all 0.5s;
  cursor: pointer;
}

button:hover {
  transform: scale(1.1);
  background: linear-gradient(180deg, hsl(0, 0%, 50%), hsl(0, 0%, 5%));
  color: #fff;
  border-color: #fff;
}

@media (max-width: 1000px) {
  .App {
    width: 80vw;
  }
}